@import url('https://fonts.googleapis.com/css2?family=MonteCarlo&family=WindSong&family=IBM+Plex+Serif:wght@300&family=Public+Sans&family=Spectral&family=Roboto:wght@100;300&display=swap');

body {
  margin: 0px;
  padding: 0px;
}
figure, figcaption,
p, ul, li, h1, h2, h3,
div article, aside, nav,
input, select, textarea,
header, footer, section {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a, [type="submit"] {
  cursor: pointer;
}


