nav ul,
nav ul li {
	display: inline-block;
}
nav {
	width: 50%;
}
nav ul {
	padding-left: 0px;
	margin: 0px;
}
nav a {
    padding: 5px;
	font-family: arial;
	text-decoration: none;
	font-weight: bold;
	font-size: 17px;
	color: #000;
	display: block;
}
nav a:hover {
	background-image: -webkit-gradient(linear, left top, right top, from(white), color-stop(white), to(white));
	background-image: -webkit-linear-gradient(left, white, white, white);
	background-image: -o-linear-gradient(left, white, white, white);
	background-image: linear-gradient(90deg, white, white, white);
	background-position: left bottom;
	background-repeat: no-repeat;
	background-size: 100% 2px;
}
@media screen and (max-width: 765px) {
	nav {
		padding-bottom: 2px;
		padding-right: 0px;
		padding-left: 0px;
		text-align: center;
		width: 100%;
	}
	nav a {
		font-size: 15px;
	}
	nav a:hover {
		background-size: 100% 1px;
	}
}
@media screen and (max-width: 675px) {
	nav a {
		font-size: 14px;
	}
}


