.contact-info {
	text-align: right;
	width: 50%;
}
.contact-text {
	display: inline-block;
}
.contact-text a {
	padding: 0px 5px;
	font-family: arial;
	text-decoration: none;
	font-weight: bold;
	font-size: 14px;
	color: #000;
}
.contact-text > a:first-child {
	padding-left: 0px;
}
.contact-text > a:last-child {
	padding-right: 0px;
}
@media screen and (max-width: 765px) {
	.contact-info {
		padding-top: 2px;
		text-align: center;
		width: 100%;
	}
	.contact-text a {
		font-size: 15px;
	}
}
@media screen and (max-width: 675px) {
	.contact-text a {
		font-size: 14px;
	}
}


