.row-top,
.colum-left,
.photo-wall,
.colum-right,
.row-bottom,
.colum-center, 
.photo-wall img,
.photo-wall figure,
.photo-wall figcaption {
	width: 100%;
}
.photo-wall,
.colum-center,
.photo-wall figure,
.photo-wall figcaption {
	display: -ms-flexbox;
	display: flex;
}
.colum-left,
.colum-right {
	margin: 5px;
}
.photo-wall {
	margin: 25px auto 25px;
	max-width: 750px;
}
.photo-wall,
.photo-wall img,
.photo-wall figure,
.photo-wall figcaption {
	height: 100%;
}
.colum-center {
	-ms-flex-direction: column;
	    flex-direction: column;
}
.row-top {
	margin-bottom: 2.5px;
	margin-top: 5px;
	height: 40%;
}
.row-bottom {
	margin-bottom: 5px;
	margin-top: 2.5px;
	height: 60%;
}
.photo-wall figure {
	position: relative;
	color: white;
	margin: 0px;
}
.photo-wall figcaption {
	background-color: rgba(0, 0, 0, .7);
	font-family: 'Public Sans';
	-ms-flex-pack: center;
	    justify-content: center;
    -ms-flex-align: center;
        align-items: center;
	position: absolute;
	font-size: 1.2em;
	opacity: 0;
}
.show-caption {
	-webkit-animation: fadein .5s ease-in forwards;
	        animation: fadein .5s ease-in forwards;
}
@-webkit-keyframes fadein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
@keyframes fadein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
.photo-wall figcaption span {
	text-align: center;
	width: 75%;
}
@media screen and (max-width: 765px) {
	.photo-wall figcaption span {
		font-size: 0.85em;
	}
}
@media screen and (max-width: 580px) {
	.photo-wall figcaption span {
		font-size: 0.75em;
	}
}



