.main-content section:not(.skylar) {
	padding: 50px 20px 0px;
}
.main-content section.contact {
	padding: 50px;
}
.main-content:nth-child(odd) section {
	background-image: url("http://localhost:3000/images/textures/linen-background.jpg");
}
.main-content:nth-child(even) section {
	background-image: url("http://localhost:3000/images/textures/45-degree-fabric-light.png");
}
hr {
	background-color: chocolate;
	margin-bottom: 50px;
	max-width: 650px;
	border: none;
	height: 2px;
	width: 100%;
}
@media screen and (max-width: 765px) {
	.main-content section:not(.skylar-section) {
		padding: 25px 20px 0px;
	}
	.main-content section.contact {
		padding: 25px 20px 35px;
	}
	hr {
		margin-bottom: 38px;
	}
}


