.location figure {
	max-width: 750px;
	-ms-flex-direction: row;
	    flex-direction: row;
	margin: 0px auto;
	display: -ms-flexbox;
	display: flex;
	width: 100%;
}
.location figure .map,
.location figure .highlights {
	min-height: 320px;
	width: 50%;
}
.location figure .map {
	background-position:  top right;
	background-size: cover;
}
.location figure .highlights {
	font-family: 'Roboto', arial;
	padding-left: 25px;
	line-height: 20px;
	font-size: 15px;
}
.location figure .highlights p,
.location figure .highlights ol {
	padding: 0px;
	margin: 0px;
}
.location figure .highlights ol {
	list-style-position: inside;
	margin-top: 15px;
	padding-left: 0px;
}
.location figure .highlights ol li:not(:last-child) {
	list-style-position: inside;
	margin-bottom: 8px;
}
@media screen and (max-width: 820px) {
	.location figure {
		-ms-flex-direction: column;
		    flex-direction: column;
	}
	.location figure .map,
	.location figure .highlights {
		max-width: 650px;
		margin: 0px auto;
		width: 100%;
	}
	.location figure .map {
		max-width: 650px;
		margin-bottom: 25px;
		width: 100%;
	}
	.location figure .highlights {
		min-height: auto;
		padding: 0px;
		width: 95%;
	}
}



