.intro-text {
	font-family: 'Roboto', arial;
	margin: 0px auto;
	text-align: center;
	max-width: 700px;
	line-height: 28px;
	font-size: 18px;
	width: 100%;
}
@media screen and (max-width: 785px) {
	.intro-text {
		max-width: 95%;
		line-height: 24px;
		font-size: 16px;
	}
}


